
export default function generateData(n, interval_sec_ms, min_range, max_range) {
    var now = new Date().getTime()
    var series = []
    var data = []
    var label = []
    for (var i = 0; i < n; i++) {
        var tmpL = now;
        var tmpD = Math.round(min_range + Math.random() * (max_range - min_range));
        series.push([
            tmpL, tmpD
        ]);
        data.push(tmpD);
        label.push(tmpL);
        now -= interval_sec_ms;
    }


    return { series, data, label };
}


export function generateHighLowGraph(n, interval_sec_ms, min_range, max_range, scale) {
    // console.log('=================> ++++++');
    var now = new Date().getTime();
    var data = [];
    var line = [];
    var scatter = [];
    var highline = [];
    var lowline = [];
    var vesopresor = [];

    for (var i = 0; i < n; i++) {
        var tmpL = now;
        var tmpD = Math.round(min_range + Math.random() * (max_range - min_range));

        var tmplow = Math.round(Math.random() * scale);
        var tmphigh = Math.round(Math.random() * scale);

        data.push({
            x: new Date(tmpL),
            y: [tmpD, tmpD + tmphigh, tmpD - tmplow, tmpD]
        })
        line.push({
            x: new Date(tmpL),
            y: tmpD
        })
        highline.push({
            x: new Date(tmpL),
            y: tmpD + tmphigh
        });
        lowline.push({
            x: new Date(tmpL),
            y: tmpD - tmplow
        });

        var vesoAmount = 45+Math.round(Math.random() * 30);
        var should_add = Math.floor(Math.random() + .2);

        if (should_add === 1) {
            vesopresor.push({
                x: new Date(tmpL),
                y: vesoAmount
            })
        } else {
            vesopresor.push({
                x: new Date(tmpL),
                y: 0
            })
        }
        now -= interval_sec_ms;
    }
    // console.log(data);
    return { data, line, scatter, highline, lowline, vesopresor };
}