<template>
  <div id="columngraph">
    <VueApexCharts
        type="bar"
        height="160px"
        width="400px"
        :options="chartOptions"
        :series="series"
    />
  </div>
</template>

<script>
import VueApexCharts from "vue-apexcharts";
export default {
  name: "ColumnGraph",
  components: {
    VueApexCharts,
  },
  data() {
    var curUTC = new Date().getTime();
    var diff = 24 * 3600 * 1000;
    var cat = [];
    for(var i = 0 ; i<15 ; i++) {
      cat.push(new Date(curUTC).toISOString());
      curUTC -= diff;
    }

    cat.reverse();

    return {
      series: [{
        name: 'Current Weight',
        data: [3, 5, 2, -2, -3, -5,-7,-8,-9,-11,-13,-14,-16,-17,-20],
        label: [3, 5, 2, -2, -3, -5,-7,-8,-9,-11,-13,-14,-16,-17,-20],
      }],
      chartOptions: {
        chart: {
          type: 'bar',
          height: 350
        },
        plotOptions: {
          bar: {
            colors: {
              ranges: [{
                from: 0,
                to: 50,
                color: '#2347b7'
              }, {
                from: -50,
                to: 0,
                color: '#7c3a7c'
              }]
            },
            columnWidth: '80%',
          }
        },
        dataLabels: {
          enabled: false,
        },
        yaxis: {
          title: {
            text: 'Weight',
          },
          labels: {
            formatter: function (y) {
              if (y===0){
                return "ADMN"
              }else{
                return y+115.5 + "kg"
              }

            }
          }
        },
        xaxis: {
          type: 'datetime',
          categories: cat,
          labels: {
            rotate: -90
          }
        }
      },
    }
  }
}
</script>

