<template>
  <div class="candle">

    <VueApexCharts :height="300" :options="chartOptions" :series="series" />
    <BarChart :b_data="b_data" />
  </div>
</template>


<script>
import VueApexCharts from "vue-apexcharts";
import { generateHighLowGraph } from "./../generator.js";
import BarChart from "./BarChart.vue";
export default {
  name: "Candle",
  components: {
    VueApexCharts,
    BarChart,
  },
  data() {
    const tmpData = generateHighLowGraph(30, 60 * 60 * 1000, 80, 120, 40);
    return {
      b_data: tmpData.vesopresor,
      series: [
        {
          name: "MAP",
          type: "candlestick",
          data: tmpData.data,
        },
        {
          name: "SBP",
          type: "scatter",
          data: tmpData.highline,
        },
        {
          name: "DBP",
          type: "scatter",
          data: tmpData.lowline,
        },
      ],
      chartOptions: {
        chart: {
          type: "scatter",
          height: "350px",
          offsetY: 0,
        },
        plotOptions: {
          candlestick: {
            colors: {
              upward: "#1300FE",
              downward: "#1300FE",
            },
            wick: {
              useFillColor: true,
            },
          },
          line: {},
        },
        title: {
          text: "Blood Pressure (mmHg)",
          align: "center",
        },
        xaxis: {
          type: "datetime",
          height: "300px",
        },
        yaxis: {
          tooltip: {
            enabled: false,
          },
          min: 50,
          max: 150,
        },
      },
    };
  },
  mounted() {
    console.log("MON  LOL==> ", this.b_data);
  },
};
</script>

<style scoped>
.candle {
  max-height: 500px;
}
