<template>
  <div class="popup" v-bind:key="rerender">
    <!--  <div id="ptitle">{{title}}</div>-->
    <div id="ptitle">{{ title }}
      <img @click=close class="cross" src="./../assets/cross.png" alt="BACK"/>
    </div>
    <Chart
        :c_data="data"
        :c_label="label"
        :c_data_type="`L/Min`"
        :c_width="1200"
        :c_height="500"
    />
  </div>
</template>

<script>
import generateData from "../generator";
import Chart from "./Chart";

export default {
  name: "PopUP",
  components: {Chart},
  props: {
    p_min: Number,
    p_max: Number,
    title: String
  },
  methods: {
    close: function () {
      window.popup_js.style.display = "none"
      // window.popup_js.style.rerender=0
    },
    forcerr: function (){
      // console.log("*****"+this.p_min);
      this.$forceUpdate()
    }
  },
  data() {
    return {
      ...generateData(50, 3600 * 1000, this.p_min, this.p_max)
      , rerender: 1
    }
  },
  mounted() {
    console.log("*****"+this.rerender);
  }
}
</script>

