<template>
  <div id="chart">
    <VueApexCharts
      type="area"
      :height="c_height"
      :options="chartOptions"
      :series="series"
      :width="c_width"
    ></VueApexCharts>
  </div>
</template>

<script>
import VueApexCharts from "vue-apexcharts";
export default {
  name: "LineChart",
  props: {
    c_data: Array,
    c_label: Array,
    c_title: String,
    c_data_type: String,
    c_height: Number,
    c_width:Number
  },
  components: {
    VueApexCharts,
  },
  data() {
    return {
      series: [
        {
          name: this.c_data_type,
          data: this.c_data,
        },
      ],
      chartOptions: {
        chart: {
          type: "area",

          zoom: {
            enabled: true,
          },
          fontFamily: "Trebuchet MS"
        },
        dataLabels: {
          enabled: false,
        },
        stroke: {
          curve: "smooth",
        },

        title: {
          text: this.c_title,
          align: "center",
          view: false
        },
        // subtitle: {
        //   text: "Price Movements",
        //   align: "left",
        // },
        labels: this.c_label,
        xaxis: {
          type: "datetime",
        },
        // yaxis: {
        //   opposite: true,
        // },
        legend: {
          horizontalAlign: "left",
        },
      },
    };
  },
  mounted() {
    console.log(" ========> ", "mounted", this.c_data);
  },
};
</script>

<style>
#chart {
  max-width: 600px;
  max-height: 300px;
  min-height: 180px;
  min-width: 400px;
